import { serverUrl } from "../../common/common.js"
export default {
	data() {
		return {
			userInfo: {}
		}
	},
	onShow () {
		var _this = this
		var globalUser = _this.globalUser('globalUser')
		_this.userInfo = globalUser
	},
	methods: {
		clearStorage () {
			uni.clearStorage()
			uni.showToast({
				title: '清理缓存成功',
				mask: false,
				duration: 1500
			})
		},
		loginOut () {
			var _this = this
			var globalUser = this.globalUser('globalUser')
			uni.request({
				url: serverUrl('user/logout', ['userId=' + globalUser.id]),
				method: "POST",
				success: (res) => {
					if (res.data.status === 200) {
						uni.removeStorageSync('globalUser')
						uni.switchTab({
							url: '../me/me'
						})
					}
				}
			})
		},
		operate () {
			var _this = this
			var globalUser = this.globalUser('globalUser')
			uni.showActionSheet({
				itemList: ["查看用户头像", "从相册选择上传"],
				success (res) {
					var index = res.tapIndex
					if (index === 0) {
						// 预览头像
						var faceArr = []
						faceArr.push(globalUser.faceImage)
						uni.previewImage({
							urls: faceArr,
							current: faceArr[0]
						})
					} else if (index === 1) {
						// 选择  头像 上传
						uni.chooseImage({
							count: 1,
							sizeType: ["compressed"],
							sourceType: ["album"],
							success (res) {
								// 获得 临时 路径
								var tempFilePath = res.tempFilePaths[0]






								uni.navigateTo({
									url: "../faceCrop/faceCrop?tempFilePath=" + tempFilePath
								})

							}
						})
					}
				}
			})
		},
		modifyNickname () {
			uni.navigateTo({
				url: "../meNickname/meNickname"
			})
		},
		modifyBirthday () {
			uni.navigateTo({
				url: "../meBirthday/meBirthday"
			})
		},
		modifySex () {
			uni.navigateTo({
				url: "../sex/sex"
			})
		}
	}
}
